<script>
import PageHeader from './PageHeader'
import PageFooter from './PageFooter'
export default {
  components: {PageHeader, PageFooter},
  data() {
    return {
      chapter: [
                [
                    {type: 't2', contents: '一、引言'},
                    {type: 'p', contents: '近年来随着互联网和计算机技术的不断发展，电子数据采集技术在临床试验中越来越多地被采用，它与传统的基于纸质的采集方式不同，具有数据及时录入、实时发现数据错误、加快研究进度、提高数据质量等优势，因此各国药品监管部门都鼓励临床试验中采用电子数据采集技术以保证数据质量。'},
                    {type: 'p', contents: '为了促进我国临床试验电子数据的完整性、准确性、真实性和可靠性符合《药物临床试验质量管理规范》和监管部门相应技术指南的原则要求，有必要对临床试验中应用电子数据采集技术的基本考虑和原则进行明确阐释，本指导原则通过对电子数据采集技术的概念和基本考虑，电子数据采集系统的基本技术要求以及在临床试验实施不同阶段的应用要求的详细阐述，旨在帮助和指导相关各方，包括申办者、合同研究组织（CRO）、临床研究者等在临床试验中规范合理地应用电子数据采集这一技术。'},   
                ],
                [
                    {type: 't2', contents: '二、电子数据采集的定义'},
                    {type: 'p', contents: '电子数据采集（Electronic Data Capture, EDC）是一种基于计算机网络的用于临床试验数据采集的技术，通过软件、硬件、标准操作程序和人员配置的有机结合，以电子化的形式直接采集和传递临床数据。'},
                    {type: 'p', contents: '随着信息技术的发展，移动电子设备如平板电脑、智能手机、扫描仪等已具备作为EDC终端的条件，EDC系统已能将基于网络的交互应答系统（IWRS）、药物警戒系统、数据分析和报告系统、试验药品管理系统等整合成一体；同时，国际公认的数据标准（如CDISC）也正在EDC中得以应用。'},   
                ],
                [
                    {type: 't2', contents: '三、应用电子数据采集技术的基本考虑'},
                    {type: 'p', contents: '当今时代，临床试验的各个阶段（从准备到研究结束）都离不开计算机化系统的辅助，而确保数据质量及其真实完整性是使用EDC系统的根本要求。'},
                    {type: 't3', contents: '（一）数据质量与真实完整性'},
                    {type: 'p', contents: '数据质量和真实完整性是对整个临床试验的有效性和安全性进行正确评价的基础，是药品监管科学的核心要素。申办者在进行电子化临床试验数据管理的过程中应建立完善的基于风险考虑的质量管理体系，并遵循数据质量的ALCOA+原则，即可归因性（Attributable）、易读性(Legible)、同时性(Contemporaneous)、原始性(Original)、准确性(Accurate)、完整性(Complete)、一致性(Consistent)、持久性(Enduring)和可获得性（Available When Needed）。'},   
                    {type: 't3', contents: '（二）系统的风险管理'},
                    {type: 'p', contents: '电子化系统的风险管理对于保证临床试验的数据质量和真实完整性十分重要。风险管理的基本过程包括风险严重性的评估、出现风险的可能性分析、实际风险的监测、风险的纠正和预防等方面。'},   
                    {type: 't3', contents: '（三）生命周期'},
                    {type: 'p', contents: 'EDC的生命周期包括两个方面，即EDC系统本身的从开发到退役的整个过程，和投入运行后的EDC系统在服务于临床试验项目过程中的运营周期。'},   
                    {type: 't4', contents: '1.系统的生命周期'},   
                    {type: 'p', contents: '一个计算机化系统有自身的生命周期，历经开发、运用、维护和退役等多个阶段。EDC系统的生命周期包括：立项、用户需求、系统需求、系统设计、系统开发、系统测试、系统发布、系统维护更新、系统退役等。计算机系统的生命周期规范是要确保系统自始至终都保持着被验证过的状态。'},   
                    {type: 't4', contents: '2.项目应用的生命周期'},   
                    {type: 'p', contents: '申办者在选择EDC系统时，应对系统进行严格的评估和审查，以确保用于临床试验的EDC系统符合本指导原则的要求。EDC系统服务商应能提供完整、规范的系统验证文档。实际项目在投入运营前应完成用户接受测试（User Acceptance Testing，UAT）。'},   
                    {type: 'p', contents: '项目应用的生命周期包括但不限于：eCRF构建、系统设置、用户接受测试、数据采集、数据核查、数据锁库、数据导出、数据及系统保存、系统下线与归档等。'},   
                ],    
      ]
    }
  },
}
</script>

